}

body {
  background: #aa9b89;
  font-family: Montserrat, sans-serif;
}

.bg {
  width: 100%;
  padding: 2rem;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  z-index: 100;
  filter: blur();
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
}

.hidden {
